import { createElement } from '@/utils/createElement';
import st from '@/styles/part.scss';

export function createPart(bgImage, sizes, data = {}, small, vertical) {
  const part = createElement('div', st.part);
  const bg = createElement('div', st.bg);
  const txt = createElement('h2', [
    st.text,
    vertical ? st.vertical : '',
    small ? st.textSmall : ''
  ].join(' '));
  part.style.height = sizes[1] + 'px';
  part.style.width = sizes[0] + 'px';
  txt.innerHTML = data.title;
  bg.innerHTML = bgImage;
  part.appendChild(bg);
  part.appendChild(txt);
  initListener(part, data.price);
  return part;
}
function initListener(elem, price) {
  const listener = (e) => {
    elem.classList.toggle(st.active);
    elem.$active = !elem.$active && true;
    calculatePrice(price, elem.$active ? 'incr' : 'decr');
  };
  elem.addEventListener('click', listener, true);
}
function calculatePrice(price = 0, action = 'incr') {
  const el = document.getElementById('box-app-price');
  const currentPrice = parseInt(el.innerHTML);
  if (action === 'incr') {
    el.innerHTML = String(currentPrice + price) + ' ₽';
  }
  if (action === 'decr') {
    el.innerHTML = String(currentPrice - price) + ' ₽';
  }
};
