import { createRow } from '@/components/row';
import { createPart } from '@/components/part';
import { createOffset } from '@/components/offset';
import { createCol } from '@/components/col';

import leftSmall from '@/svg/top-row/left-small.svg';
import smallLeftTongue from '@/svg/top-row/small-left-tongue.svg';
import leftTongue from '@/svg/top-row/left-tongue.svg';
import rightTongue from '@/svg/top-row/right-tongue.svg';

import data from '@/prices';
const { top } = data;

const leftCol = createCol([240, 112 + 44], [
  createPart(smallLeftTongue, [240, 44], top.leftSmallTongue, true),
  createPart(leftSmall, [240, 112], top.leftSmall)
]);
const lTongue = createPart(leftTongue, [126, 82], top.leftTongue, true);
const rTongue = createPart(rightTongue, [126, 82], top.rightTongue, true);
const offset = createOffset([240, 25]);

const rowTop = createRow([leftCol, lTongue, offset, rTongue], {
  paddingLeft: '62px',
  height: 112 + 44 + 'px',
  alignItems: 'flex-end'
});

export default rowTop;
