import { createElement } from '@/utils/createElement';
import st from '@/styles/col.scss';

export function createCol(sizes, innerElements) {
  const col = createElement('div', st.col);
  col.style.height = sizes[1] + 'px';
  col.style.width = sizes[0] + 'px';
  for (const elem of innerElements) {
    col.appendChild(elem);
  }
  return col;
}
