import { createRow } from '@/components/row';
import { createPart } from '@/components/part';

import big from '@/svg/center-row/big.svg';
import bigWithRecess from '@/svg/center-row/big-recess.svg';
import small from '@/svg/center-row/small.svg';
import smallLeftTongue from '@/svg/center-row/small-left-tongue.svg';

import data from '@/prices';
const { center } = data;

const left = createPart(smallLeftTongue, [62, 256], center.leftTongue, true, true);
const bigLeft = createPart(big, [240, 256], center.leftBig);
const smallLeft = createPart(small, [126, 256], center.leftSmall, false, true);
const centerWithRecess = createPart(bigWithRecess, [240, 256], center.rightBig);
const smallRight = createPart(small, [126, 256], center.rightSmall, false, true);

const rowCenter = createRow([left, bigLeft, smallLeft, centerWithRecess, smallRight], {
  height: '256px'
});

export default rowCenter;
