import { createRow } from '@/components/row';
import { createPart } from '@/components/part';
import { createOffset } from '@/components/offset';
import { createCol } from '@/components/col';

import leftTongue from '@/svg/bottom-row/left-tongue.svg';
import rightTongue from '@/svg/bottom-row/right-tongue.svg';
import centerSmall from '@/svg/bottom-row/center-small.svg';
import centerTongue from '@/svg/bottom-row/small-center-tongue.svg';

import data from '@/prices';
const { bottom } = data;

const offset = createOffset([302, 25]);
const lTongue = createPart(leftTongue, [126, 82], bottom.leftTongue, true);
const centerCol = createCol([240, 112 + 44], [
  createPart(centerSmall, [240, 112], bottom.centerSmall),
  createPart(centerTongue, [240, 44], bottom.centerSmallTongue, true)
]);
const rTongue = createPart(rightTongue, [126, 82], bottom.rightTongue, true);

const rowBottom = createRow([offset, lTongue, centerCol, rTongue], {
  height: 112 + 44 + 'px'
});

export default rowBottom;
